.nav-wrapper{
    position: relative;
    width: 100vw;
    height: 80px;
    padding: 0px 120px;
    border-bottom: 1px solid rgba(0, 0, 0, 0.5);
}
.nav-container{
    width: 100%;
    height: 100%;
    display: grid;
    grid-template-columns: max-content auto max-content max-content;
    border-left: 1px solid rgba(0, 0, 0, 0.5);
    border-right: 1px solid rgba(0, 0, 0, 0.5);
}
.logo-wrapper{
    position: relative;
    grid-column: 1;
    width: 100%;
    height: 100%;
    padding: 0px 50px;
    display: grid;
}
.logo{
    margin: auto;
    font-size: 3em;
    font-family: 'Karla';
    font-weight: 700;
}
.page-links-wrapper{
    position: relative;
    grid-column: 2;
    width: 100%;
    padding: 0px 50px;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    border-left: 1px solid rgba(0, 0, 0, 0.5);
    border-right: 1px solid rgba(0, 0, 0, 0.5);
}
.home,.products,.reviews{
    font-size: 1.5em;
    font-family: 'Karla'
}
.nav-btn{
    grid-column: 4;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    border-left: 1px solid rgba(0, 0, 0, 0.5);
    cursor: pointer;
}
.contacts-btn{
    font-size: 1.5em;
    font-family: 'Karla';
    margin: auto 10px;
    z-index: 102;
}
.carts-btn,.wishlist-btn{
    position: relative;
    margin: auto 10px;
}
.cartItemsNo{
    position: absolute;
    top: -10px;
    right: -10px;
    width: 20px;
    height: 20px;
    border-radius: 50%;
    color: #FFEDD2;
    background-color: #141414;
    display: flex;
    align-items: center;
    justify-content: center;
    font-family: 'Karla',sans-serif;
}
.link{
    text-decoration: none;
    color: black;
}
.cart-container{
    position: absolute;
    width: 30%;
    height: fit-content;
    min-height: 100vh;
    background-color: #FFEDD2;
    right: -2px;
    top: 80px;
    border-top: 1px solid rgba(0,0,0,0.5);
    border-left: 1px solid rgba(0,0,0,0.5);
    z-index: 99;
    transition: all 0.3s ease-in-out;
}
.cart-container-hide{
    transform: translateX(100%);
}
.cart-container-show{
    transform: translateX(0)
}
.form-container{
    position: absolute;
    top: 0;
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: rgba(0, 0, 0, 0.75);
    z-index: 101;
}
.form-hide{
    display: none;
}
.form-close-btn{
    position: absolute;
    width: 100px;
    height: 100px;
    top: 80px;
    right: 200px;
    cursor: pointer;
    transition: all 0.3s ease-in-out;
}
.form-close-btn::after,
.form-close-btn::before{
    position: absolute;
    content: '';
    width: 100%;
    height: 2px;
    top: 0px;
    background-color: #FFEDD2;
}
.form-close-btn::after{
    transform: rotate(45deg)
}
.form-close-btn::before{ 
    transform: rotate(-45deg)
}
.hamburger-menu,
.hamburger-menu-active{
    position: relative;
    width: 40px;
    height: 40px;
}
.hamburger-menu::after,
.hamburger-menu::before,
.hamburger-menu-active::after,
.hamburger-menu-active::before{
    position: absolute;
    right: 0;
    top: 35%;
    content: '';
    width: 35px;
    height: 2px;
    background-color: rgb(0,0,0);
    transition: all 0.3s ease-in-out;
}
.hamburger-menu::after{
    right: 0;
    width: 20px;
    margin-top: 10px;
}
/*click */
.hamburger-menu-active::before{
    transform: rotate(-45deg)
}
.hamburger-menu-active::after{
    margin: 0;
    width: 35px;
    transform: rotate(45deg)
}
.hamburger-menu,.hamburger-menu-active{
    display: none;
}
/*mobile nav*/
.mobile-nav-wrapper{
    z-index: 101;
    position: absolute;
    width: 100%;
    height: 100%;
    background-color: var(--background-color);
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.mobile-home,.mobile-products,.mobile-reviews{
    font-size: 3em;
    font-family: 'Karla';
    margin-bottom: 2em;
}
.mobile-contact-btn{
    font-size:2em;
    font-family: 'Karla';
    border: 1px solid rgb(0,0,0);
    padding: 10px;
}
.mobile-nav-wrapper{
    display: none;
}
.mobile-nav-wrapper-hide{
    display: none;
}
@media only screen and (max-width: 1000px){
    .hamburger-menu,.hamburger-menu-active{
        display: block;
        margin-right: 5px;
    }
    .mobile-nav-wrapper{
        display: flex;
    }
    .nav-wrapper{
        padding: 0px 20px;
    }
    .nav-container{
        grid-template-columns: repeat(3,1fr);
    }
    .page-links-wrapper,.contacts-btn{
        display: none;
    }
    .logo-wrapper{
        padding: 5px;
        border-right: 1px solid rgba(0, 0, 0, 0.5);
    }
    .logo{
        font-size: 2em;
    }
    .nav-btn{
        grid-column: 3;
        padding: 0px 5px;
    }
    .cart-container{
        position: fixed;
        width: 100%;
    }
    .form-close-btn{
        right: 30px;
    }
}