.form{
    position: relative;
    width: 300px;
    height: 500px;
    background-color: #FFEDD2;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: flex-start;
    padding: 100px 50px;
}
.form-title{
    font-size: 1.5em;
    font-weight: bold;
    font-family: 'Karla',sans-serif;
}
.email-input,.username-input,.message-input{
    outline: none;
    border: none;
    border-bottom: 1px solid rgb(0,0,0);
    background-color: transparent;
    padding: 0px 0px 10px 5px;
}
.submit-btn{
    background-color: transparent;
    padding: 5px;
    font-size: 1.2em;
    font-family: 'Karla',sans-serif;
}