.category-wrapper{
    position: relative;
    width: 100%;
    display: grid;
    cursor: pointer;
    white-space: nowrap;
}
.category{
    margin: auto 50px;
    font-size: 1.5em;
    font-family: 'Karla';
}
.category-dropdown{
    position: absolute;
    font-family: 'Karla',sans-serif;
    width: 100%;
    top: 100%;
    font-size: 1.2em;
    line-height: 100%;
    padding: 10px 10px;
    border: 1px solid rgba(0, 0, 0, 0.5);
    border-top: none;
    z-index: 100;
    background-color: #FFEDD2;
}
.ct-btn{
    padding: 10px 0px;
}
.mensclothing:hover,
.womensclothing:hover,
.jewellery:hover,
.electronics:hover{
    background-color: #978B7A;
}
@media only screen and (max-width: 428px){
    .category-wrapper{
        grid-column: 2;
    }
    .category{
        font-size: 1.2em;
        margin: auto;
        padding: 0px 5px;
    }
    .category-dropdown{
        width: 100vw;
        left: -100%;
    }
    .product-ct-dpd{
        left: 0%;
        top:100%;
        margin-top: 10px;
        margin-left: -10px;
    }
}